export class JobApplicantLanguageViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#job_applicant_language_view_container',

			fluency_array: null,
			competency_array: null,
			qualification_array: null,

			qualification_api: null,

			sub_view_grid_autosize: true
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'JobApplicantLanguageEditView.html';
		this.permission_id = 'job_applicant';
		this.viewId = 'JobApplicantLanguage';
		this.script_name = 'JobApplicantLanguageView';
		this.table_name_key = 'job_applicant_language';
		this.context_menu_name = $.i18n._( 'Languages' );
		this.navigation_label = $.i18n._( 'Language' ) + ':';
		this.api = TTAPI.APIJobApplicantLanguage;
		this.qualification_api = TTAPI.APIQualification;
		this.render();

		//call init data in parent view
		if ( !this.sub_view_mode ) {
			this.initData();
		}

		//this.setSelectRibbonMenuIfNecessary( 'UserContact' )
	}

	showNoResultCover( show_new_btn ) {
		super.showNoResultCover( ( this.sub_view_mode ) ? true : false );
	}

	onGridSelectRow() {
		if ( this.sub_view_mode ) {
			this.buildContextMenu( true );
			this.cancelOtherSubViewSelectedStatus();
		} else {
			this.buildContextMenu();
		}
		this.setDefaultMenu();
	}

	onGridSelectAll() {
		if ( this.sub_view_mode ) {
			this.buildContextMenu( true );
			this.cancelOtherSubViewSelectedStatus();
		}
		this.setDefaultMenu();
	}

	cancelOtherSubViewSelectedStatus() {
		switch ( true ) {
			case typeof ( this.parent_view_controller.sub_job_applicant_education_view_controller ) !== 'undefined':
				this.parent_view_controller.sub_job_applicant_education_view_controller.unSelectAll();
			case typeof ( this.parent_view_controller.sub_job_applicant_membership_view_controller ) !== 'undefined':
				this.parent_view_controller.sub_job_applicant_membership_view_controller.unSelectAll();
			case typeof ( this.parent_view_controller.sub_job_applicant_skill_view_controller ) !== 'undefined':
				this.parent_view_controller.sub_job_applicant_skill_view_controller.unSelectAll();
			case typeof ( this.parent_view_controller.sub_job_applicant_license_view_controller ) !== 'undefined':
				this.parent_view_controller.sub_job_applicant_license_view_controller.unSelectAll();
				break;
		}
	}

	onMassEditClick() {

		var $this = this;
		$this.is_add = false;
		$this.is_viewing = false;
		$this.is_mass_editing = true;
		LocalCacheData.current_doing_context_action = 'mass_edit';
		$this.openEditView();
		var filter = {};
		var grid_selected_id_array = this.getGridSelectIdArray();
		var grid_selected_length = grid_selected_id_array.length;
		this.mass_edit_record_ids = [];

		$.each( grid_selected_id_array, function( index, value ) {
			$this.mass_edit_record_ids.push( value );
		} );

		filter.filter_data = {};
		filter.filter_data.id = this.mass_edit_record_ids;

		this.api['getCommon' + this.api.key_name + 'Data']( filter, {
			onResult: function( result ) {
				var result_data = result.getResult();

				$this.unique_columns = {};

				$this.linked_columns = {};

				if ( !result_data ) {
					result_data = [];
				}

				if ( $this.sub_view_mode && $this.parent_key ) {
					result_data[$this.parent_key] = $this.parent_value;
				}

				$this.current_edit_record = result_data;
				$this.initEditView();

			}
		} );
	}

	onAddClick() {

		if ( this.sub_view_mode ) {
			this.buildContextMenu( true );
		}

		super.onAddClick();
	}

	initOptions() {
		var $this = this;

		this.initDropDownOption( 'fluency' );
		this.initDropDownOption( 'competency' );

		var args = {};
		var filter_data = {};
		filter_data.type_id = [40];
		args.filter_data = filter_data;
		this.qualification_api.getQualification( args, {
			onResult: function( res ) {
				res = res.getResult();

				$this.qualification_array = res;
			}
		} );
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_language': { 'label': $.i18n._( 'Language' ) },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APIJobApplicantLanguage,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_job_applicant_language',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_language = this.edit_view_tab.find( '#tab_language' );

		var tab_language_column1 = tab_language.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_language_column1 );

		var form_item_input;

		// Language
		var args = {};
		var filter_data = {};
		filter_data.type_id = [40];
		args.filter_data = filter_data;

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );
		form_item_input.AComboBox( {
			api_class: TTAPI.APIQualification,
			allow_multiple_selection: false,
			layout_name: 'global_qualification',
			show_search_inputs: true,
			set_empty: true,
			field: 'qualification_id'
		} );

		form_item_input.setDefaultArgs( args );
		this.addEditFieldToColumn( $.i18n._( 'Language' ), form_item_input, tab_language_column1, '' );

		// Fluency
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'fluency_id' } );
		form_item_input.setSourceData( $this.fluency_array );
		this.addEditFieldToColumn( $.i18n._( 'Fluency' ), form_item_input, tab_language_column1 );

		// Competency
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'competency_id' } );
		form_item_input.setSourceData( $this.competency_array );
		this.addEditFieldToColumn( $.i18n._( 'Competency' ), form_item_input, tab_language_column1 );

		// Description
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_AREA );
		form_item_input.TTextArea( { field: 'description', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Description' ), form_item_input, tab_language_column1, '', null, null, true );

		form_item_input.parent().width( '45%' );

		//Tags
		form_item_input = Global.loadWidgetByName( FormItemType.TAG_INPUT );

		form_item_input.TTagInput( { field: 'tag', object_type_id: 393 } );
		this.addEditFieldToColumn( $.i18n._( 'Tags' ), form_item_input, tab_language_column1, '', null, null, true );
	}

	searchDone() {

		super.searchDone();

		TTPromise.resolve( 'JobApplicant_Qualifications_Tab', 'JobApplicantLanguageViewController' );

	}

}

JobApplicantLanguageViewController.loadSubView = function( container, beforeViewLoadedFun, afterViewLoadedFun ) {
	Global.loadViewSource( 'JobApplicantLanguage', 'SubJobApplicantLanguageView.html', function( result ) {
		var args = {};
		var template = _.template( result );

		if ( Global.isSet( beforeViewLoadedFun ) ) {
			beforeViewLoadedFun();
		}
		if ( Global.isSet( container ) ) {
			container.html( template( args ) );
			if ( Global.isSet( afterViewLoadedFun ) ) {
				afterViewLoadedFun( sub_job_applicant_language_view_controller );
			}
		}
	} );
};